// chat window scss
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.web-chat-option {
    overflow: hidden;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    $this: &;

    &--cursor-pointer{
        cursor: pointer;
    }
    &--cursor-default{
        cursor: default;
    }
    .eureka-list-item__inner {
        @include eureka-flex-row;
        padding: 1.6rem;
    }

    .eureka-icon-chevron-right{
        color: var(--web-chat-option-icon-chevron-right-color);
        font-size: 3rem;
        margin-right: -8px;
    }

    &__option-container {
        border-bottom: 1px solid var(--web-chat-option-border-color);
    }
    
    &__option-container:last-child {
        border: none;
    }

    &__text {
        font-weight: 400;
        font-size: 1.7rem;
        line-height: 2.1rem;
        color: var(--web-chat-option-text-color);
        flex: none;
        align-self: stretch;
        flex-grow: 0;
        margin: 0 0 0 8px;
    }

    &__meta {
        font-weight: 400;
        font-size: 1.1rem;
        line-height: 1.4rem;
        color: var(--web-chat-option-meta-color);
        align-self: stretch;
        flex-grow: 0;
        margin: 0 0 0 8px;
    }

    &__text-meta-container {
        width: 100%;
    }
}
