// choose option window scss
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.choose-option-window {
    $this: &;
    
    &__header-container {
       margin-bottom: 16px;
       
        &-title {
            font-style: normal;
            font-weight: 600;
            font-size: 2rem;
            line-height: 2.5rem;
            letter-spacing: -0.03em;
            color: var(--eureka-core-color-black);
        }
    }

    &__content-container {
        border: 1px solid var(--web-chat-option-border-color);
        border-radius: var(--web-chat-option-radius);
    }

    &__svg-container {
        margin-top: 3px;
    }

    .eureka-list-item {
        background-color: unset;
    }
}
